import * as React from 'react'
import { graphql } from 'gatsby'
import { SiteLayout } from '../components/SiteLayout'
import { PostCard } from '../components/PostCard'
import { Title, Line } from '../components/styled-components'

import './index.css'

const IndexPage = ({ data }) => {
  const {
    allMarkdownRemark: { edges }
  } = data
  return (
    <SiteLayout>
      <Title>Articles</Title>
      <Line />
      {edges.map(
        ({
          node: {
            frontmatter: { date, title, thumbnail },
            timeToRead,
            fields: { slug }
          }
        }) => (
          <PostCard
            slug={slug}
            thumbnail={thumbnail.childImageSharp.fluid}
            title={title}
            minsToRead={timeToRead}
            datePublished={date}
          />
        )
      )}
    </SiteLayout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark {
      pageInfo {
        perPage
      }
      edges {
        node {
          fields {
            slug
          }
          timeToRead
          frontmatter {
            title
            thumbnail_alt
            thumbnail {
              childImageSharp {
                fluid(maxWidth: 800) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            date
            slug
            meta_title
            seo_description
            seo_keywords
          }
        }
      }
    }
  }
`
export default IndexPage
