import * as React from 'react'
import { Card, Icon } from '@blueprintjs/core'
import { navigate } from 'gatsby'
import Img from 'gatsby-image'
import { Subtitle, Line } from './styled-components'
import styled from 'styled-components'
import { getSpacing } from '../stylesheet'

const InfoSpan = styled.div`
  display: flex;
  justify-content: space-between;
`
const StyledIcon = styled(Icon)`
  margin-right: ${getSpacing(2)};
`

export const PostCard = ({
  title,
  slug,
  thumbnail,
  minsToRead,
  datePublished
}) => {
  const displayDate = new Date(datePublished).toLocaleDateString('en-GB', {
    month: 'short',
    day: 'numeric'
  })
  const onNavigate = () => navigate(slug)
  return (
    <Card elevation={2} interactive={true} onClick={onNavigate}>
      <Img fluid={thumbnail} />
      <Subtitle>{title}</Subtitle>
      <Line />
      <InfoSpan>
        <div>
          <StyledIcon color="white" icon="calendar" />
          {displayDate}
        </div>
        <div>
          <StyledIcon color="white" icon="person" />
          James Haworth Wheatman
        </div>
        <div>
          <StyledIcon color="white" icon="time" />
          {minsToRead} mins to read
        </div>
      </InfoSpan>
    </Card>
  )
}
